import React, { Component } from 'react';
import Logo from '../img/michi.jpg';    
import Sms from './Sms';
import FormSms from './FormSms';
import "./styles/chat.css";
export default class Chat extends Component {
    constructor(props) {
        super(props);
    }
    // array=[ <Sms img={Logo} message="Hola, quieres adivinar mi edad?"/>];
    render() {
        // this.array.push(<Sms img={Logo} message="Hola, quieres adivinar mi edad?"/>);
        return (
            <React.Fragment>
                <div className="Chat">
                    <div className="Sms__container">
                        {this.props.Sms}
                    </div>
                    <FormSms numMax={this.props.max} numMin={this.props.min} onClick={this.props.onClickFormSms}  />
                </div>
            </React.Fragment>
        )
    }
}
