import React, { Component } from 'react';
import './styles/button.css';
export default class Button extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             
        }
        this.hello=this.hello.bind(this);
    }
    hello(e){
        e.preventDefault();
        if (this.props.numRam==this.props.value) {
            alert("Ganaste");
            window.location.reload();
            // Location.reload();
        }else{
            alert("perdiste");
            // Location.reload();
            window.location.reload();
        }
        // console.log("hello");
    }    
    render() {
        return (
            <div>
                <form onSubmit={this.hello}>
                    <button className={"Button "+this.props.stateButton} >{this.props.value}</button>
                </form>
            </div>
        )
    }
}
