import React, { Component } from 'react';
import LayoutButtons from '../components/LayoutButtons';
import Chat from '../components/Chat';
import Sms from '../components/Sms';
export default class MichiAge extends Component {
    constructor(props) {
        super(props);
        this.state = {
            inicialNumber:50,
            maxNumber:50,
            minNumber:1,
            numTries:4,
            numberRandom:parseInt(Math.random()*(50-1)+1),
            form:{
                number:undefined,
                sign:undefined
            },
            smsArray:[<Sms message="Hola Â¿Quieres adivinar mi edad?" styleType="Michi"/>,<Sms message="Solo puedes hacerme 4 preguntas" styleType="Michi"/>]
        }
        this.handleClickFormSms=this.handleClickFormSms.bind(this);
        this.resposeMesssage=this.resposeMesssage.bind(this);
        this.countTries=this.countTries.bind(this);
    }
    handleClickFormSms(formSms){
        if (this.state.numTries!=0) {
            if (formSms.sign.value==">"  ){
                setTimeout(() => {
                    this.resposeMesssage("Tu edad es mayor a  "+formSms.number.value+" ?","User");
                }, 300);
                this.setState({form:{number:parseInt(formSms.number.value,10) ,sign:formSms.sign.value,numTries:--this.state.numTries}});
                if (this.state.numberRandom > formSms.number.value) {
                    setTimeout(() => {
                        this.resposeMesssage("Sii!!","Michi");
                        setTimeout(() => {
                            this.countTries();
                        }, 500);
                    }, 1500);
                    this.setState({minNumber:parseInt(formSms.number.value,10)+1});
                    
                    // this.resposeMesssage("Tu edad !!","Michi");
                }
                else{ 
                    setTimeout(() => {
                        this.setState({maxNumber:parseInt(formSms.number.value,10)});
                        this.resposeMesssage("Nooo !!","Michi");
                        setTimeout(() => {
                            this.countTries();
                        }, 500);
                    }, 1500);   
                }
            }else if(formSms.sign.value=="<"){
                setTimeout(() => {     
                    this.resposeMesssage("Tu edad es menor a "+formSms.number.value +" ?","User");
                }, 300);
                this.setState({form:{number:parseInt(formSms.number.value,10) ,sign:formSms.sign.value,numTries:--this.state.numTries}});
                if (this.state.numberRandom < formSms.number.value) {
                    this.setState({maxNumber:parseInt(formSms.number.value,10)-1});
                    setTimeout(() => {
                        this.resposeMesssage("Siiii !! ","Michi");
                        setTimeout(() => {
                            this.countTries();
                        }, 500);
                    }, 1500);
                }
                else{
                    setTimeout(() => {
                        this.setState({minNumber:parseInt(formSms.number.value,10)});
                        this.resposeMesssage("Nooo !!","Michi");
                        setTimeout(() => {
                            this.countTries();
                        }, 500);
                    }, 1500);
                }
            }
        }else{
            setTimeout(() => {
                this.resposeMesssage("Ya no tienes mas preguntas escoje un numero !!","Michi");
                
            }, 500);
        }
    }
    resposeMesssage(textContent, sender){
        this.setState( (state)=>{
            return {smsArray:[...state.smsArray, <Sms message={textContent} styleType={sender} id={state.smsArray.length}/>]}
        });        
    }
    countTries(){
        if (this.state.numTries!=1 && this.state.numTries >  0) {
            this.resposeMesssage("Solo te quedan "+this.state.numTries+" preguntas","Michi");
        }else if(this.state.numTries==1){
            this.resposeMesssage("Solo te queda "+this.state.numTries+" pregunta","Michi");
        } else if (this.state.numTries==0) {
            this.resposeMesssage("Ya no tienes preguntas, es ahora de que escojas uno de los numeros disponibles","Michi");
        }
    }
    render(){
        return (
            <div>
                <LayoutButtons numberButtons={this.state.inicialNumber} max={this.state.maxNumber} min={this.state.minNumber} numRam={this.state.numberRandom} />
                <Chat max={this.state.maxNumber} min={this.state.minNumber} onClickFormSms={this.handleClickFormSms} Sms={this.state.smsArray} />         
            </div>
        )
    }
}