import React from 'react'
import "./styles/Sms.css";
export default function Sms(props) {
    var classSmsContent="Sms__content ";
    
    return (
        <div className="Sms" id={props.id}>
            <p className={classSmsContent+props.styleType}> {props.message} </p>
        </div>
    )
}
