import React, { Component } from 'react';
import "./styles/formSms.css";
export default class FormSms extends Component {
    constructor(props) {
        super(props);
        this.handleSubmit=this.handleSubmit.bind(this);
    }
    handleSubmit(e){
        e.preventDefault();

        this.props.onClick(e.target);
    }
    render() {
        return (
            <form className="FormSms"  onSubmit={this.handleSubmit}>
                <p className="FormSms__message">Tu edad es 
                    <select name="sign" className="FormSms__select">
                        <option value=">">Mayor</option>
                        <option value="<">Menor</option>
                    </select>
                    a
                    <input type="number" required name="number" min={this.props.numMin} max={this.props.numMax} className="FormSms__number" placeholder="0"></input>
                    <button className="FormSms__button">Enviar</button>
                </p>
            </form>
        )
    }
}

