import React from 'react'
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
  } from "react-router-dom";
import MichiAge from '../page/MichiAge';
import NotFound from './NotFound';
import Layout from './Layout';
export default function App() {

    return (
        <Router>
            <Layout>
                <Switch>
                    {/* <Route exact path="/" component={MichiAge}/> */}
                    <Route exact path="/michicubico" component={MichiAge}/>
                    <Route  component={MichiAge}/>
                </Switch>
            </Layout>
      </Router>
    )
}
