import React, { Component, createElement } from 'react'
import  Button from './Button';
import './styles/layoutBtn.css';
export class LayoutButtons extends Component {
    classBtn="";
    showButtons(numButtons=80){
        var listButtons=[];
        for (let index = 1; index <= numButtons; index++) {
            this.classBtn= "";
           if (index < this.props.min) {
               this.classBtn="Button__inactive";
            }
            if (index > this.props.max) {
               this.classBtn="Button__inactive";
            }
            listButtons.push(<Button value={index} stateButton={this.classBtn} numRam={this.props.numRam}/>); 
        }
        return listButtons;
    }
    render() {
        // var elemnts=this.showButtons();
        return <div className="ContainerBtn"> {this.showButtons(this.props.numberButtons)}</div>  ;       
    }
}
export default LayoutButtons;
